.blog-container {
    padding: 2rem;
    max-width: 900px;
    margin: auto;
    font-family: 'Segoe UI', sans-serif;
  }
  
  .subtitle {
    color: #666;
    margin-bottom: 2rem;
  }
  
  .post-list {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  .post-card,
  .post-form,
  .blog-container,
  .snippet,
  .full-post,
  .post-list {
    text-align: left;
  }
